/** @format */
.row {
  margin-left: 20px;
}

.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  object-fit: contain; /*늘리고 줄여도 비율 유지됨*/
  width: 100%;
  max-height: 100px;
  margin-right: 10px;
  transition: transform 400ms;
}

.row__poster:hover {
  transform: scale(1.1);
}

.row__posterLarge {
  max-height: 250px;
}

.row__posterLarge:hover {
  transform: scale(1.08);
}

h2 {
  color: #fff;
}
